.courses {
	display: -ms-flexbox;
	display: flex;
	padding-right: 3%;
	padding-left: 3%;
}

.courses_titile {
	width: 10%;
}

.courses_cards {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	width: 90%;
}

.courses_card {
	padding-right: 40px;
	padding-left: 40px;
	padding-bottom: 65px;
	padding-top: 65px;
	text-align: left;
	width: 20%;
}

#basics {
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(2, 171, 243, 0.7)), to(rgba(24, 90, 157, 0.24)));
	background: -webkit-linear-gradient(top, rgba(2, 171, 243, 0.7) 0%, rgba(24, 90, 157, 0.24) 100%);
	background: -o-linear-gradient(top, rgba(2, 171, 243, 0.7) 0%, rgba(24, 90, 157, 0.24) 100%);
	background: linear-gradient(180deg, rgba(2, 171, 243, 0.7) 0%, rgba(24, 90, 157, 0.24) 100%);
	border-radius: 25px;
}

#oop {
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 0, 92, 0.819)), to(rgba(168, 207, 228, 0.666)));
	background: -webkit-linear-gradient(top, rgba(255, 0, 92, 0.819) 0%, rgba(168, 207, 228, 0.666) 100%);
	background: -o-linear-gradient(top, rgba(255, 0, 92, 0.819) 0%, rgba(168, 207, 228, 0.666) 100%);
	background: linear-gradient(180deg, rgba(255, 0, 92, 0.819) 0%, rgba(168, 207, 228, 0.666) 100%);
	border-radius: 25px;
}

#android {
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(19, 209, 16, 0.792)), to(rgba(51, 24, 157, 0.224)));
	background: -webkit-linear-gradient(top, rgba(19, 209, 16, 0.792) 0%, rgba(51, 24, 157, 0.224) 100%);
	background: -o-linear-gradient(top, rgba(19, 209, 16, 0.792) 0%, rgba(51, 24, 157, 0.224) 100%);
	background: linear-gradient(180deg, rgba(19, 209, 16, 0.792) 0%, rgba(51, 24, 157, 0.224) 100%);
	border-radius: 25px;
}

.courses_card_title {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	padding: 0;
}

.courses_card_title > img {
	-ms-flex-item-align: center;
	    align-self: center;
	margin: 0 auto;
	width: 69px;
	height: 69px;
}

.courses_card_title > p {
	font-family: Roboto;
	font-weight: bold;
	margin-bottom: 0 10;
	line-height: 45px;
	font-size: 48px;
	color: #FFFFFF;
}

.courses_card_duration {
	margin: 0;
	font-family: Roboto;
	font-weight: bold;
	font-size: 55px;
	color: #FFFFFF;
}

.courses_card_text {
	font-family: Roboto;
	font-weight: 300;
	font-size: 19px;
	color: #FFFFFF;
}

.courses_title_div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 10%;
}

.courses_title {
	font-family: Roboto;
	font-weight: bold;
	line-height: 90px;
	font-size: 96px;
	color: #000000;
	-webkit-transform: rotate(-90deg);
	    -ms-transform: rotate(-90deg);
	        transform: rotate(-90deg);
	margin: 0;
}


