@import url('https://fonts.googleapis.com/css?family=Roboto');

.hero {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
}

.hero_left {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 40%;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-line-pack: center;
	    align-content: center;
	padding-left: 5%;
}

.hero_right {
	display: -ms-flexbox;
	display: flex;
	width: 60%;
}

.hero_right > img {
	width: 100%;
}

.main_text {
	width: 80%;
	font-family: Roboto;
	font-weight: bold;
	line-height: 135px;
	font-size: 144px;
	text-align: left;
	color: #000000;
}

.button {
	float: left;
	width: 300px;
	height: 51px;
}

.button_container {
	display: -ms-flexbox;
	display: flex;
	height: 51px;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	color: white;
	background: #1BE9C4;
	border-radius: 50px;
}

.button_container > img {
	height: 31px;
	margin-right: 12px;
}

.button_text {
	font-family: Roboto;
	font-weight: bold;
	font-size: 22px;
	color: #FFFFFF;
}


